import React, { Component } from 'react';
import Header from '../../Layout/Header';
import nuclearAcidIcon from './images/nuclearAcid.png';
import './nuclearAcidQuery.css';
import { HttpClient } from '../../../Api/httpClient'
import wx from 'weui.js';
import CryptoJS from 'crypto-js';

export default class NuclearAcidQuery extends Component {
    constructor(props) {
        super(props);
        this.state = {
            realName: '',
            idNumber: '',
            rnaReport: []
        };
    }
    componentWillMount() {
        console.log('');
    }
    componentDidMount() {
        console.log('');
        window.dg = CryptoJS; // 
    }
    getNuclearAcid() {
        if (this.state.realName === '' || this.state.idNumber === '') {
            wx.alert('');
            return;
        }
        let loading = wx.loading('');
        let that = this;
        let send_at = (new Date().getFullYear()) + '-' + (new Date().getMonth() + 1) +  '-' + (new Date().getDate()) + ' ' + (new Date().getHours() >= 10 ? new Date().getHours() : '0'+(new Date().getHours())) + ':00:00';
        send_at = new Date(send_at).getTime() / 1000;
        HttpClient.post('http://106.12.131.105:8080/v1/healthy/nuclear-acid?token=' + CryptoJS.MD5(send_at.toString()).toString(), {
            realName: that.state.realName,
            idNumber: that.state.idNumber
        }).then((response) => {
            loading.hide();
            response.json().then((res) => {
                console.log(res);
                switch (res.code) {
                    case 200:
                        if (res.data.length === 0) {
                            wx.alert('()');
                        } else {
                            that.setState({
                                rnaReport: res.data
                            });
                        }
                        break;
                    case 400:
                        wx.alert(res.msg);
                        break;
                    case 401:
                        wx.alert('');
                        break;
                    case 403:
                        wx.alert('');
                        break;
                    case 502:
                        wx.alert('');
                        break;
                    default:
                        console.log(res);
                        wx.alert(''+res.code);
                        break;
                }
            }).catch((err) => {
                console.log(err);
                wx.alert('');
            })
        }).catch((error) => {
            loading.hide();
            console.log(error);
            wx.alert('');
        });
    }
    render() {
        if (this.state.rnaReport.length === 0) {
            return (
                <div>
                    <Header appTitle=""/>
                    <div className="page__hd" data-r-healthy>
                        <img className="weui-media-box__thumb" src={nuclearAcidIcon} alt={'NuclearAcidIcon'}/>
                        <div className="page__title" data-r-healthy></div>
                        <div className="page__desc" data-r-healthy></div>
                    </div>
                    <div className="page__bd" style={{textAlign: 'center'}}>
                        <div className="weui-cells">
                            <div className="weui-cell weui-cell_input">
                                <div className="weui-cell__hd">
                                    <div className="weui-label"></div>
                                </div>
                                <div className="weui-cell__bd">
                                    <input className="weui-input" type="text" placeholder=""
                                           onChange={(event) => {
                                               this.setState({
                                                   realName: event.target.value
                                               })
                                           }} value={this.state.realName}/>
                                </div>
                            </div>
                            <div className="weui-cell weui-cell_input">
                                <div className="weui-cell__hd">
                                    <div className="weui-label"></div>
                                </div>
                                <div className="weui-cell__bd">
                                    <input className="weui-input" type="text" placeholder=""
                                           onChange={(event) => {
                                               this.setState({
                                                   idNumber: event.target.value
                                               })
                                           }} value={this.state.idNumber}/>
                                </div>
                            </div>
                            <div className="weui-cell weui-cell_input">
                                <button className="weui-btn weui-btn_primary weui-btn_inline"
                                        onClick={this.getNuclearAcid.bind(this)}>
                                </button>
                                <button className="weui-btn weui-btn_default weui-btn_inline"></button>
                            </div>
                            <div className="weui-cell weui-cell_input">
                                <ul style={{fontSize: '10px', padding: '10px', textAlign: 'left'}}>
                                    <li></li>
                                    <li></li>
                                    <li>1860</li>
                                    <li></li>
                                    <li>H5</li>
                                    <li></li>
                                </ul>
                            </div>
                            <div style={{fontSize: '10px', textAlign: 'center'}}>
                                   
                            </div>
                        </div>
                    </div>
                </div>
            );
        } else {
            return (
                <div>
                    <Header appTitle="" />
                    <div className="page__hd" data-r-healthy>
                        <img className="weui-media-box__thumb" src={nuclearAcidIcon} alt={'NuclearAcidIcon'}/>
                        <div className="page__title" data-r-healthy>{this.state.realName} </div>
                        <div className="page__desc" data-r-healthy></div>
                    </div>
                    <div className="page__bd" style={{textAlign: 'center'}}>
                        <div className="weui-panel weui-panel_access">
                            <div className="weui-panel__hd" onClick={() => {
                                this.setState({ rnaReport: [] })
                            }}>
                                
                            </div>
                            <div className="weui-panel__bd" style={{textAlign: 'left'}}>
                                {this.state.rnaReport.map((v, i) => {
                                    return (
                                        <div className="weui-media-box weui-media-box_text" key={i}>
                                            <div className="weui-media-box__title weui-media-box__title_in-text"> - {v.result}</div>
                                            <div className="weui-media-box__bd">{new Date(v.receivetime).toLocaleString()} </div>
                                            <div className="weui-media-box__bd">{new Date(v.checktime).toLocaleString()}</div>
                                            <div className="weui-media-box__desc">{v.jgmc}</div>
                                        </div>
                                    );
                                })}
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
    }
}





